.navbar {
  display: flex;
  justify-content: space-around;
  padding: 16px;
  box-shadow: 0 1px 3px -2px black;
  align-items: center;
}

.nav-logo {
  display: flex;
  align-items: center;
  gap: 10px;
}

.nav-logo p {
  color: #141414;
  font-size: 38px;
  font-weight: 600;
}

.nav-menu {
  display: flex;
  align-items: center;
  list-style-type: none;
  gap: 50px;
  font-size: 20px;
  font-weight: 500;

  li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    a {
      text-decoration: none;
      color: var(--gray);
    }
  }
}

.nav-menu hr {
  border: none;
  width: 80%;
  height: 3px;
  margin-top: 7px;
  border-radius: 10px;
  background-color: #ff4141;
}

.nav-login-cart {
  display: flex;
  align-items: center;
  gap: 45px;
}

.nav-login-cart button {
  width: 157px;
  height: 58px;
  outline: none;
  border: 1px solid #7a7a7a;
  border-radius: 75px;
  color: #515151;
  font-size: 20px;
  font-weight: 500;
  background-color: white;
  cursor: pointer;
}

.nav-login-cart button:active {
  background: #ff4141;
}

.nav-cart-count {
  width: 22px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -35px;
  margin-left: -55px;
  border-radius: 11px;
  font-size: 14px;
  background-color: red;
  color: white;
}

.nav-dropdown {
  display: none;
}

/*Responsive*/
@media screen and (max-width: 1280px) {
  .navbar {
    padding: 12px 50px;
  }
  .nav-logo {
    img {
      width: 40px;
    }
    p {
      font-size: 25px;
    }
  }

  .nav-menu {
    gap: 30px;
    font-size: 16px;
  }
  .nav-login-cart {
    gap: 30px;
    button {
      width: 120px;
      height: 45px;
      font-size: 16px;
    }
  }
  .nav-login-cart .nav-cart-count {
    margin-left: -40px;
    font-size: 12px;
  }
}

@media screen and (max-width: 1024px) {
  .navbar {
    padding: 12px 30px;
  }
  .nav-menu {
    gap: 25px;
    font-size: 14px;
  }
  .nav-login-cart {
    button {
      width: 80px;
      height: 35px;
      font-size: 14px;
    }
    img {
      width: 30px;
    }
  }
  .nav-cart-count {
    width: 18px;
    height: 18px;
  }
}

@media screen and (max-width: 800px) {
  .navbar {
    padding: 10px 0;
  }

  .nav-dropdown {
    display: block;
    width: 30px;
    rotate: -90deg;
    transition: 0.5s;
  }

  .nav-menu {
    display: none;
    height: 80px;
    width: 100%;
    position: absolute;
    background: white;
    justify-content: center;
    top: 50px;
  }

  .nav-menu-visible {
    display: -webkit-flex;
  }

  .nav-dropdown.open {
    rotate: 0deg;
  }
}

@media (max-width: 500px) {
  .navbar {
    padding: 8px 0px;
    gap: 0;
  }

  .nav-logo {
    transform: scale(0.8);
  }

  .nav-menu {
    height: 70px;
    top: 50px;
  }

  .nav-login-cart {
    transform: scale(0.8);
  }
}
