.hero {
  height: 100vh;
  background: linear-gradient(180deg, var(--primary), #e1ffea22 60%);
  display: flex;
}

.hero-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  padding-left: 180px;
  line-height: 1.1;

  h2 {
    color: #090909;
    font-size: 26px;
    font-weight: 600;
  }

  p {
    color: #171717;
    font-size: 100px;
    font-weight: 700;
  }
}

.hero-hand-icon {
  display: flex;
  align-items: center;
  gap: 20px;

  img {
    width: 105px;
  }
}

.hero-latest-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  width: 310px;
  height: 70px;
  border-radius: 75px;
  margin-top: 30px;
  background: #171717;
  color: var(--white);
  font-size: 22px;
  font-weight: 500;
}

.hero-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 1280px) {
  .hero-left {
    padding-left: 100px;

    h2 {
      font-size: 22px;
    }

    p {
      font-size: 60px;
    }
  }

  .hero-hand-icon img {
    width: 80px;
  }

  .hero-latest-btn {
    gap: 10px;
    width: 250px;
    margin-top: 20px;
    font-size: 18px;
  }

  .hero-right img {
    width: 500px;
  }
}

@media screen and (max-width: 1024px) {
  .hero-left {
    padding-left: 80px;

    h2 {
      font-size: 20px;
    }

    p {
      font-size: 50px;
    }
  }

  .hero-hand-icon img {
    width: 65px;
  }

  .hero-latest-btn {
    width: 250px;
    height: 50px;
    font-size: 16px;
  }

  .hero-right img {
    width: 400px;
  }
}

@media screen and (max-width: 800px) {
  .hero-left {
    padding-left: 30px;

    h2 {
      font-size: 16px;
    }

    p {
      font-size: 40px;
    }
  }

  .hero-hand-icon img {
    width: 50px;
  }

  .hero-latest-btn {
    width: 175px;
    height: 40px;
    font-size: 13px;
  }

  .hero-right img {
    width: 300px;
  }
}

@media screen and (max-width: 500px) {
  .hero {
    flex-direction: column;
  }

  .hero-right {
    display: flex;
  }

  .hero-left {
    h2 {
      font-size: 18px;
    }

    p {
      font-size: 50px;
    }
  }

  .hero-hand-icon {
    img {
      width: 55px;
    }
  }

  .hero-latest-btn {
    width: 200px;
    height: 45px;
    font-size: 14px;
  }
}
