.offers {
  width: 65%;
  height: 60vh;
  display: flex;
  margin: auto;
  padding: 0px 140px;
  margin-bottom: 150px;
  background: linear-gradient(180deg, var(--primary), #e1ffea22 60%);
}

.offers-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;

  h1 {
    color: var(--black);
    font-size: 80px;
    font-weight: 600;
  }

  p {
    color: var(--black);
    font-size: 22px;
    font-weight: 600;
  }

  button {
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background-color: var(--black);
    border: none;
    color: var(--white);
    font-size: 22px;
    font-weight: 500;
    font-family: "Red Hat Text", sans-serif;
    margin-top: 30px;
    cursor: pointer;
  }
}

.offers-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 50px;
}

/*Responsive*/
@media screen and (max-width: 1200px) {
  .offers {
    padding: 0px 80px;
    margin-bottom: 120px;
  }
  .offers-left {
    h1 {
      font-size: 40px;
    }

    p {
      font-size: 18px;
    }

    button {
      width: 220px;
      height: 50px;
      font-size: 18px;
    }
  }

  .offers-right {
    padding-top: 20px;
    img {
      width: 300px;
    }
  }
}

@media screen and (max-width: 1024px) {
  .offers {
    padding: 0px 60px;
    margin-bottom: 80px;
    height: 40vh;
  }
  .offers-left {
    h1 {
      font-size: 30px;
    }

    p {
      font-size: 16px;
    }

    button {
      width: 160px;
      height: 40px;
      font-size: 16px;
    }
  }

  .offers-right {
    padding-top: 10px;

    img {
      width: 200px;
    }
  }

  @media screen and (max-width: 800px) {
    .offers {
      margin-bottom: 60px;
    }
    .offers-left {
      h1 {
        font-size: 22px;
      }

      p {
        font-size: 13px;
      }

      button {
        width: 140px;
        height: 30px;
        font-size: 12px;
      }
    }

    .offers-right {
      img {
        width: 180px;
      }
    }
  }

  @media screen and (max-width: 500px) {
    .offers {
      height: 25vh;
      margin-bottom: 50px;
    }
    .offers-left {
      h1 {
        font-size: 16px;
      }

      p {
        font-size: 10px;
      }

      button {
        width: 120px;
        height: 25px;
        font-size: 10px;
        margin-top: 12px;
      }
    }

    .offers-right {
      img {
        width: 120px;
      }
    }
  }
}
