.productdisplay {
  display: flex;
  margin: 0px 170px;
}

.productdisplay-left {
  display: flex;
  gap: 17px;
}

.productdisplay-img-list {
  display: flex;
  flex-direction: column;
  gap: 16px;

  img {
    height: 163px;
  }
}

.productdisplay-main-img {
  width: 586px;
  height: 700px;
}

.productdisplay-right {
  margin: 0px 70px;
  display: flex;
  flex-direction: column;

  h1 {
    color: var(--black);
    font-size: 40px;
    font-weight: 700;
  }

  button {
    padding: 20px 40px;
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    color: var(--white);
    background: var(--primary);
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;
  }
}

.productdisplay-right-star {
  display: flex;
  align-items: center;
  margin-top: 13px;
  gap: 5px;
  color: var(--gray);
  font-size: 16px;
}

.productdisplay-right-prices {
  display: flex;
  margin: 40px 0px;
  gap: 3px;
  font-size: 24px;
  font-weight: 700;
}

.productdisplay-right-price-old {
  color: var(--gray);
  text-decoration: line-through;
}
.productdisplay-right-price-new {
  color: var(--primary);
}

.productdisplay-right-size h1 {
  margin-top: 55px;
  color: var(--gray);
  font-size: 20px;
  font-weight: 600;
}

.productdisplay-right-sizes {
  display: flex;
  margin: 30px 0px;
  gap: 20px;

  div {
    padding: 18px 24px;
    background: white;
    border: 1px solid var(--secondary);
    border-radius: 3px;
    cursor: pointer;
  }
}

.productdisplay-right-category {
  margin-top: 10px;

  span {
    font-weight: 600;
  }
}

@media screen and (max-width: 1280px) {
  .productdisplay {
    margin: 0 60px;
  }

  .productdisplay-left {
    gap: 10px;
  }

  .productdisplay-img-list {
    gap: 10px;

    img {
      height: 120px;
    }
  }

  .productdisplay-main-img {
    width: auto;
    height: 510px;
  }

  .productdisplay-right {
    margin: 0px 30px;

    h1 {
      font-size: 22px;
    }

    button {
      width: 150px;
      padding: 15px 0px;
      margin-bottom: 20px;
    }
  }

  .productdisplay-right-stars {
    gap: 3px;
    font-size: 13px;
  }

  .productdisplay-right-prices {
    margin: 10px 0px;
    font-size: 18px;
  }

  .productdisplay-right-description {
    font-size: 13px;
  }

  .productdisplay-right-size {
    h1 {
      margin-top: 20px;
      font-size: 20px;
    }
  }

  .productdisplay-right-sizes {
    div {
      padding: 14px 20px;
    }
  }

  .productdisplay-right-category {
    margin-top: 5px;
    font-size: 14px;
  }
}

@media screen and (max-width: 1024px) {
  .productdisplay {
    margin: 0 30px;
  }

  .productdisplay-left {
    gap: 5px;
  }

  .productdisplay-img-list {
    img {
      height: 80px;
    }
  }

  .productdisplay-main-img {
    height: 350px;
  }

  .productdisplay-right {
    h1 {
      font-size: 18px;
    }

    button {
      width: 120px;
      padding: 10px 0px;
      margin-bottom: 10px;
      font-size: 14px;
    }
  }

  .productdisplay-right-stars img {
    width: 15px;
  }

  .productdisplay-right-description {
    font-size: 12px;
  }

  .productdisplay-right-size {
    h1 {
      margin-top: 20px;
      font-size: 20px;
    }
  }

  .productdisplay-right-sizes {
    margin-top: 10px 0px;
    div {
      padding: 10px 16px;
      font-size: 12px;
    }
  }

  .productdisplay-right-category {
    font-size: 12px;
  }
}

@media screen and (max-width: 800px) {
  .productdisplay {
    margin: 0 10px;
  }

  .productdisplay-left {
    gap: 0px;
    flex-direction: column-reverse;
    align-items: center;
  }

  .productdisplay-img-list {
    flex-direction: row;
    gap: 6px;
    img {
      height: 70px;
    }
  }

  .productdisplay-main-img {
    height: 300px;
  }

  .productdisplay-right {
    h1 {
      font-size: 14px;
    }

    button {
      width: 100px;
      font-size: 12px;
    }
  }

  .productdisplay-right-stars img {
    width: 13px;
  }

  .productdisplay-right-description {
    font-size: 10px;
  }

  .productdisplay-right-size {
    h1 {
      margin-top: 10px;
      font-size: 20px;
    }
  }

  .productdisplay-right-sizes {
    margin-top: 10px 0px;
    gap: 5px;
    div {
      padding: 4px 11px;
    }
  }

  .productdisplay-right-category {
    font-size: 10px;
  }
}

@media screen and (max-width: 500px) {
  .productdisplay {
    flex-direction: column;
  }

  .productdisplay-left {
    gap: 10px;
    flex-direction: row;
    margin: auto;
  }

  .productdisplay-img-list {
    flex-direction: column;
    gap: 8px;

    img {
      height: 75px;
    }
  }

  .productdisplay-main-img {
    height: 330px;
  }

  .productdisplay-right {
    margin: 5px;

    h1 {
      margin-top: 15px;
      font-size: 20px;
      font-weight: 500;
    }

    button {
      width: 130px;
      font-size: 15px;
      padding: 12px 0px;
    }
  }

  .productdisplay-right-stars {
    width: 15px;
  }

  .productdisplay-right-sizes {
    gap: 10px;
    margin: 20px 0px;

    div {
      padding: 10px 16px;
      font-size: 14px;
    }
  }

  .productdisplay-right-category {
    font-size: 16px;
  }
}
