.relatedproducts {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  height: 90vh;

  h1 {
    color: var(--black);
    font-size: 50px;
    font-weight: 600;
  }

  hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    color: var(--black);
  }
}

.relatedproducts-item {
  margin-top: 50px;
  display: flex;
  gap: 30px;
}

@media screen and (max-width: 1280px) {
  .relatedproducts {
    height: 70vh;

    h1 {
      font-size: 40px;
    }

    hr {
      width: 160px;
      height: 4px;
    }
  }

  .relatedproducts-item {
    gap: 20px;
    margin-top: 30px;
  }
}

@media screen and (max-width: 1024px) {
  .relatedproducts {
    height: 60vh;

    h1 {
      font-size: 30px;
    }

    hr {
      width: 120px;
      height: 3px;
    }
  }

  .relatedproducts-item {
    gap: 15px;
    margin-top: 20px;
  }
}

@media screen and (max-width: 800px) {
  .relatedproducts {
    height: 50vh;
    gap: 6px;

    h1 {
      font-size: 20px;
    }

    hr {
      width: 100px;
    }
  }

  .relatedproducts-item {
    gap: 5px;
  }
}

@media screen and (max-width: 500px) {
  .relatedproducts {
    height: 90vh;
  }

  .relatedproducts-item {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
}
